body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0)
}

div {
  margin-bottom: 15px;
}

label {
  margin-right: 5px;
}
button{
  display: inline=block;
}
.cta-container{
  text-align: center;;
}
.cta-container a{
margin-left:15px ;

}
.cta-container a:nth-child(1){
margin-left:0px ;
}
.btn{
  text-align: center;
    display: inline-block;
    position: relative;
    text-decoration: none;
    color: #000;
    text-transform: capitalize;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    padding: 15px 10px;
    min-width: 150px;
    border-radius: 6px;
    overflow: hidden;
    border:0px;
}
.btn.green{
  background-color: #22b573;
  color: #fff;
}
.btn.blue{
  background-color: #0574bd;
  color: #fff;
}
.btn.dark-blue{
  background-color: #004a7b;
  color: #fff;
}
.form-section-head div{
font-size: 18px;
font-weight: bold;
}

/* INPUT */

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=submit] {
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
